<template>
  <div id="app">
    <router-view/>
    <img src="~@/assets/images/close@2x.png" v-show="play" class="contr" @click="musicPlay" alt="">
    <img src="~@/assets/images/open@2x.png" v-show="!play" class="contr" @click="musicPause" alt="">
    <audio v-show="false" class="media-audio" loop ref="MusicPlay">
        <source src="~@/assets/images/music.mp3" type="audio/mpeg">
    </audio>
  </div>
</template>

<script>
export default {
    name: 'App',
    data(){
        return {
            play:false
        }
    },
    methods:{
        musicPause() {
            this.$refs.MusicPlay.play();
            this.play = true;
        },
        musicPlay() {
            this.$refs.MusicPlay.pause();
            this.play = false
        },
    }
}
</script>

<style>
@font-face {
    font-family: NotoSerifCJKscBlack;
    src: url('./assets/fonts/NotoSerifCJKsc-Black.otf')
}
@font-face {
    font-family: NotoSerifCJKscLight;
    src: url('./assets/fonts/NotoSerifCJKsc-Light.otf')
}
@font-face {
    font-family: NotoSerifCJKscRegular;
    src: url('./assets/fonts/NotoSerifCJKsc-Regular.otf')
}
.contr{
    width: 82px;
    height: 82px;
    position: fixed;
    right: 0;
    top: 14.5px;
}
</style>



